import { useState } from "react";
import ReactCardFlip from "react-card-flip";
import { Box, Typography, Button } from "@mui/material";

const FlipCard = (props) => {
  const [isFlipped, setIsFlipped] = useState(false);

  const handleClick = (e) => {
    e.preventDefault();
    setIsFlipped(!isFlipped);
  };

  return (
    <ReactCardFlip isFlipped={isFlipped} flipDirection="vertical">
      <Box className="challenge_card" onClick={handleClick}>
        <Typography className="f_xl fw_500 ff_md c_card ls_ss mt_1">
          {props.name}
        </Typography>
        <Typography className="f_lg fw_500 ff_md c_secondary ls_ss mt_1">
          {props.distance}
        </Typography>
      </Box>

      <Box className="challenge_card p_1" onClick={handleClick}>
        <Typography className="f_lg ff_md ls_sm">
          {props.description}
        </Typography>
      </Box>
    </ReactCardFlip>
  );
};

export default FlipCard;
